				},
				dialogImageUrl: '',
        		dialogVisible: false
			}
		},
		methods: {
			handleSubmit(){
				console.log(1)
			},
			handleReset(){
				this.$router.push({path:'/submitInfoList'})
			},
			handleSubmitInfo(){
				this.$router.push({path:'/addInfo'})
			},
			onSubmit() {
				//console.log('submit!');
			},
			handleRemove(file, fileList) {
				//console.log(file, fileList);
			},
			handlePictureCardPreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
			}
			
		}
	}
</script>

<style lang="scss" scope>
.width_limit{
	min-width:109px;
}
.el-form .el-form-item{
	position: relative;
}
.must_write::before{
	content:"*";
	font-size:1em;
	color:red;
	position:absolute;
	margin:12px 30px 0;
	z-index:1;
}
._fixmust::before{
	margin:12px 0px 0;
	z-index:1;
}
._spin::before{
	margin-left: -8px;
}
._spin3::before{
	margin-left: 12px;
}
._spin4::before{
	margin-left: 46px;
}
</style>


