<template>

  <el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="" name="first">
      <el-form
        ref="form"
        :model="form"
        label-width="110px"
        @submit.prevent="onSubmit"
        style="margin:20px;width:60%;min-width:600px;"
      >
        <el-form-item class="must_write" label=":">
          <el-input v-model="form.merchants_name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item class="must_write _fixmust" label="/:">
          <el-input v-model="form.org_name" checked placeholder="/"></el-input>
        </el-form-item>

        <el-form-item class="must_write" label=" ">
          <el-input v-model="form.brand" placeholder=""></el-input>
        </el-form-item>

        <el-form-item class="must_write" label="">
          <el-radio-group v-model="form.edu_type">
            <el-radio :label="1" ></el-radio>
            <el-radio :label="2" ></el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item class="must_write _fixmust" label="">
          <el-radio-group v-model="form.org_type">
            <el-radio :label="1" ></el-radio>
            <el-radio :label="2"></el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item class="must_write _spin4" label=" ">
          <el-input v-model="form.org_code" placeholder=""></el-input>
        </el-form-item>

        <el-form-item class="must_write _fixmust _spin" label=" ">
          <el-input v-model="form.brand_name" placeholder=""></el-input>
        </el-form-item>

        <el-form-item 
          v-model="form.org_license_time" 
          class="must_write _spin4" 
          label="" >
          <el-col :span="12">
            <el-date-picker
              type="date"
              placeholder=""
              v-model="form.startime"
              style="width: 100%;"
            ></el-date-picker>
          </el-col>
          
          <el-col :span="12">
            <el-date-picker
              type="date"
              placeholder=""
              v-model="form.endtime"
              style="width: 100%;"
            ></el-date-picker>
          </el-col>
          
          <el-radio-group v-model="form.org_license_time">
            <!-- <el-radio label="" :prop="checktimestate"></el-radio> -->
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
        <!--  -->
        <el-form-item class="must_write" label="">
          <el-upload
            tip=""
            limit=1
            :headers=headers
            name=media
            action="https://jsonplaceholder.typicode.com/posts/"
            list-type="picture-card"
            :on-preview="handlePictureCardPreview"
            :on-remove="handleRemove"
            :on-success="handleLicenseSuccess"
            :on-error="PicLicenseErr"
          >
            <i  class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt />
          </el-dialog>
        </el-form-item>

        <el-form-item class="must_write _spin3" label=":">
          <el-input v-model="form.owner_name" placeholder=""></el-input>
        </el-form-item>

        <el-form-item ref="insertNumer" class="must_write _fixmust" label="">
          <el-row>
            <el-input style="width:80%" v-model.number="insertNumer.owner_phone" 
            placeholder="" 
            prop="owner_phone">
            </el-input>

            <el-button  
            class="validateCode" 
            @click="sendMsg('insertNumer')" 
            :disabled="isDisabled">{{buttonName}}</el-button>

          </el-row>
        </el-form-item>
        <el-form-item class="must_write must_write _spin3" label="">
          <el-row>
            <el-input style="width:80%" v-model="insertNumer.validateCode" placeholder=""></el-input>
            <!-- <el-button style="width:14%"></el-button> -->
          </el-row>
        </el-form-item>

        <el-form-item class="must_write _spin3" label="">
          <el-input v-model="form.owner_mail" placeholder=""></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary"></el-button>
          <el-button @click.native.prevent></el-button>
        </el-form-item>
      </el-form>
    </el-tab-pane>

    <el-tab-pane @click.native.prevent="handleCheckState" label="" name="second" align-center>
        <el-steps :active="2" align-center finish-status="success">
        <el-step title="" description="2017-07-19 15:43:23"></el-step>
        <el-step title="" description="2017-07-19 15:43:23"></el-step>
        <el-step title="" description=""></el-step>
      </el-steps>

      <el-row>
        <el-button @click="handleSubmitedInfo"></el-button>
        <el-button @click="reReque"></el-button>
        <el-button type="success" @click="handleClickNext"></el-button>
      </el-row>
    </el-tab-pane>
  </el-tabs>
</template>

<script>
 // import { phoneCode } from '../api/api';
 import axios from 'axios';
export default {
  data() {
  var CheckTel = (rule, value, callback) => {
if (!value) {
callback(new Error(''));
} else if (!Number.isInteger(value)) {
callback(new Error(''));
} else if (value.toString().length != 11) {
callback(new Error('11'));
} else {
callback();
}}

    return { 
    disabled: true,
buttonName: "",
isDisabled: false,
time: 10,
    insertNumer:{
        owner_phone: '15313321103',
validateCode: '0101',
},
rules: {
        owner_phone: [{
required: true,
validator: CheckTel,
trigger: 'blur'
}],
validateCode: [{
required: true,
message: '',
trigger: 'blur'
}],
},
      activeName: "first",
      checktimestate:"0",
      startime: "2019-10-01",
      endtime: "2020-10-01",
      form: {
        // 
        merchants_name: "",
        //
        org_name: "",
        brand: "",
        edu_type: "1",
        //edutype1
        org_type: "1",
        org_code:"293928382838x",
        owner_name:"",
        owner_mail:"01@qq.com",
        org_license_time:"",
        //
        org_license:"https:\/\/midpf-account.cdn.bcebos.com\/5a0a785735d5183b44809ebe7074035a.jpeg",
      },
      dialogImageUrl: "",
      dialogVisible: false,
    }
  },
  computed:{
    headers() {
      return {
        'ContentType': 'multipart/form-data'
      }
    }
  },
  methods: {
    handleCheckState(){
      //shop_view
      axios.get("http://192.168.3.253:8081/openApi/shop_view",{"shop_id":"121",
      "merchants_id":"77064127556400"}).then(res=>res.data)
      .then(data=>{
        let {name,status,audit_time,audit_msg}=data;
        console.log(status)
      })
    },
    sendMsg(insertNumer) {
      axios.get("http://192.168.3.253:8081/sms/sendMessage",{"phone":"15313321103"}).then(res=>res.data)
      .then(data=>{
        let {status,msg,code}=data;
        console.log(code)
      });
    this.$refs.insertNumer.validate('owner_phone',valid=>{
    if (valid!=='') {
    this.rules.owner_phone;
    }else {
    let me = this;
    me.isDisabled = true;
    let interval = window.setInterval(function () {
    me.buttonName = me.time + '';
    --me.time;
    if (me.time < 0) {
    me.buttonName = "";
    me.time = 10;
    me.isDisabled = false;
    window.clearInterval(interval);
      }
      }, 1000);
      }
      });
    },
    reReque() {
      console.log("");
    },
    handleClickNext() {
      this.$router.push({ path: "/submitInfoList" });
    },
    handleSubmitedInfo() {
      this.$router.push({ path: "/submitedInfoReview" });
    },
    handleClick(tab, event) {
      //console.log(tab, event);
    },
    onSubmit() {
      //console.log('submit!');
    },
    handleRemove(file, fileList) {
      //console.log(file, fileList);
     
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    handleLicenseSuccess(res,file){
      console.log(res)
    },
    PicLicenseErr(err,file,fileList){
      console.log(err)
    }
  }
};
</script>

<style lang="scss" scope>
.width_limit {
  min-width: 109px;
}
.el-form .el-form-item {
  position: relative;
}
.must_write::before {
  content: "*";
  font-size: 1em;
  color: red;
  position: absolute;
  margin: 12px 30px 0;
  z-index: 1;
}
._fixmust::before {
  margin: 12px 0px 0;
  z-index: 1;
}
._spin::before {
  margin-left: -8px;
}
._spin3::before {
  margin-left: 12px;
}
._spin4::before {
  margin-left: 46px;
}
</style>


